import React from 'react';
import { AiOutlineLock, AiOutlineUser } from 'react-icons/ai';
import { Link, } from 'react-router-dom';
import Confirmation from '../../components/Confirmation/Confirmation';
import DefaultInfo from '../../components/DefaultInfo/DefaultInfo';
// style import 
import '../../styles/login.css';


export default function LoginPage({ regNum, pwd, regVal, pwdVal, formHandler, closeHandler, defaultInfo, checkPassword, checkRegNum }) {
  return (
    <div className='login-container'>

        {/* <Confirmation /> */}

        {
            defaultInfo ? <DefaultInfo closeHandler={closeHandler}/> : null
        }

        

        <form onSubmit={formHandler}>

            <center><AiOutlineUser size={90} color='#d3d3d3'/></center>
            <h3 style={{textAlign: 'center'}}>sign in to continue</h3>

            { checkPassword && checkRegNum ?  <center><p className='warning' >invalid userID/password</p></center> : null }

            {/* {  ?  <center><p className='warning' >invalid userID/password</p></center> : null } */}

            

            <section>
                <span> <AiOutlineUser size={24}/></span>
                <input placeholder='Registration number' required value={regNum} onChange={regVal}/>
            </section>
            <section>
                <span> <AiOutlineLock size={24}/></span>
                <input type={'password'} placeholder='Password' required value={pwd} onChange={pwdVal} />
            </section>
            <section>
                <button type={'submit'} >Submit</button>
            </section>
            <a href='#forgot-account'>forgotten password?</a>
            <div className='a'>Not a member? <Link to='/register'>Create Account</Link></div>

        </form>
    </div>
  )
}
